@font-face {
  font-family: TitilliumWeb;
  src: url(fonts/TitilliumWeb-Regular.ttf);
}
@font-face {
  font-family: Lato;
  src: url(fonts/Lato-Regular.ttf);
}
body {
  color: #252525;
}
h2 {
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 15px;
  span {
    color: #3333ff;
  }
}
.title-block {
  position: relative;
}
svg {
  fill: #8080808c;
}
section {
  margin-top: 60px;
  padding-bottom: 60px;
  border-bottom: 0.5px solid #8080808c;
  position: relative;
  .container {
    @media screen and (min-width: 1200px) {
      width: 1200px;
    }
  }
}

.date {
  font-style: italic;
}

header {
  .nav {
    position: relative;
    box-shadow: -2px -5px 15px grey;
    height: 90px;
    justify-content: flex-end;
    @media screen and (max-width: 768px) {
      justify-content: center;
    }
    .nav-item {
      display: flex;
      align-items: center;

      & ~ .nav-item {
        margin-left: 20px;
      }

      &:hover {
        cursor: pointer;
        .nav-link {
          color: #1f467a;
        }
      }

      &:last-child {
        margin-right: 10%;
      }

      .nav-link {
        color: black;
        text-transform: uppercase;
        font: 16px TitilliumWeb, sans-serif;
      }
    }
  }
}

.portfolio-hello {
  h1 {
    font-weight: 700;
    margin-bottom: 20px;
    span {
      color: #3333ff;
    }
  }

  ul {
    padding: 0;

    & + ul {
      margin-left: 10%;
    }
    li {
      margin-top: 10px;
      list-style: none;

      span {
        font-weight: 700;
      }
    }
  }
  img {
    width: 65%;

    @media screen and (max-width: 768px) {
      height: 50%;
    }
    @media screen and (max-width: 375px) {
      height: 100%;
    }
  }
}

.portfolio-skills {
  h3 {
    margin-bottom: 20px;
  }
  .mb-50 {
    margin-bottom: 50px;
    @media screen and (max-width: 375px) {
      margin-bottom: 0;
    }
  }
  img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 50%;
    @media screen and (max-width: 768px) {
      display: none;
    }
  }
}
.portfolio-experience {
  span {
    font-size: 16px;
    font-weight: 400;
    color: grey;
  }
}
.portfolio-education {
  img {
    position: absolute;
    top: 25%;
    left: 5%;
    width: 40%;
    @media screen and (max-width: 768px) {
      display: none;
    }
  }
  .stage {
    margin-bottom: 50px;
    h3 {
      font-size: 24px;
      margin-bottom: 2px;
      span {
        font-size: 16px;
        font-weight: 400;
        color: grey;
      }
    }
    .date {
      margin-bottom: 7px;
    }
    .description {
      font-size: 20px;
    }
  }
}

.portfolio-works {
  h2 {
    margin-bottom: 50px;
  }
  .item-block {
    margin-bottom: 50px;
    &:hover {
      cursor: pointer;
      .img-wrapper {
        img {
          transform: scale(1.3);
          transition: 1s linear;
        }
      }
    }
    a {
      text-decoration: none;
      color: #252525;
    }
    .img-wrapper {
      overflow: hidden;
    }
    h5 {
      margin: 20px 0 0;
    }
    img {
      width: 100%;
    }
  }
}

footer {
  padding: 15px 0;
  background-color: #efefef;
  p {
    margin: 0;
  }
}
